
  <template>
  <v-row align="center" justify="center">
    <v-col cols="12" sm="8" md="4">
      <v-card class="elevation-12">
        <v-toolbar color="grey darken-3" dark flat>
          <v-toolbar-title>Регистрация</v-toolbar-title>
          <div class="flex-grow-1"></div>
        </v-toolbar>
        <v-card-text>
          <v-form v-model="valid">
            <v-text-field
              label="Имя"
              name="first_name"
              type="text"
              color="grey darken-3"
              v-model="first_name"
              :rules="rules.firstnameRule"
            ></v-text-field>
            <v-text-field
              label="Фамилия"
              name="last_name"
              type="text"
              color="grey darken-3"
              :rules="rules.lastnameRule"
              v-model="last_name"
            ></v-text-field>
            <v-text-field
              label="Электронная почта"
              name="email"
              type="email"
              color="grey darken-3"
              :error-messages="error"
              :rules="rules.emailRules"
              @change="error=''"
              v-model="email"
            ></v-text-field>
            <v-text-field
              id="password"
              label="Пароль"
              name="password"
              type="password"
              v-model="password"
              color="grey darken-3"
              :rules="rules.passwordRule"
            ></v-text-field>
            <v-text-field
              id="password_repeat"
              label="Повторите_пароль"
              name="password"
              type="password"
              color="grey darken-3"
              :rules="rules.passwordconfirmRule"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn
            color="grey darken-3"
            :dark="valid"
            @click="register_user"
            :disabled="!valid"
          >Зарегистрироваться</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      first_name: "",
      last_name: "",
      email: "",
      password: "",
      password_confirm: "",
      error: "",
      valid: false,
      rules: {
        emailRules: [
          v => !!v || "Требуется электронная почта",
          v => /.+@.+\..+/.test(v) || "Невалидная электронная почта"
        ],
        firstnameRule: [v => !!v || "Требуется имя"],
        lastnameRule: [v => !!v || "Требуется фамилия"],
        passwordRule: [v => !!v || "Требуется пароль"],
        passwordconfirmRule: [v => this.password == v || "Пароли не совпадают!"]
      }
    };
  },
  methods: {
    ...mapActions("UserData", ["register_user_action"]),
    register_user() {
      this.register_user_action({
        first_name: this.first_name,
        last_name: this.last_name,
        email: this.email,
        password: this.password
      }).then(x => {
        if (x) {
          this.$router.push("/");
        } else {
          this.error = "Электронная почта уже существует";
        }
      });
    }
  }
};
</script>