
export default {
    namespaced: true,
    actions: {
        async login_user_action(ctx, { email, password }) {
            var result = await fetch(`https://freeticket.herokuapp.com/login?email=${email}&password=${password}`)
            result = await result.json()
            if (result.type == "bad") {
                return false
            } else {
                result.email = email
                result.password = password
                ctx.commit("authorize", result)
                return true
            }
        },
        async register_user_action(ctx, { first_name, last_name, email, password }) {
            var result = await fetch(`https://freeticket.herokuapp.com/register?email=${email}&password=${password}&first_name=${first_name}&last_name=${last_name}`)
            result = await result.json()
            if (result.items == "bad") {
                return false
            } else {
                return true
            }
        },
        async logout_user_action(ctx) {
            ctx.commit("logout")
        },
        async addEventAction(ctx, params) {
            params.token = ctx.state.token
            var data = await fetch("https://freeticket.herokuapp.com/add_event", {
                method: "POST",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(params)
            }).then(r => r.json())
            return data.event_id
        },
        async editEventAction(ctx, params) {
            params.token = ctx.state.token
            var data = await fetch("https://freeticket.herokuapp.com/edit_event", {
                method: "POST",
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(params)
            }).then(r => r.json())
            return data.event_id
        },
        async deleteEventAction(ctx, event_id) {
            var data = await fetch(`https://freeticket.herokuapp.com/remove_event?token=${ctx.state.token}&id=${event_id}`)
        },
        async getMyEvents(ctx) {
            var data = await fetch(`https://freeticket.herokuapp.com/my_events?token=${ctx.state.token}`).then(r => r.json())
            ctx.commit("setMyEvents", data.items)
        },
        async getIsBought(ctx, event_id) {
            var data = await fetch(`https://freeticket.herokuapp.com/bought?token=${ctx.state.token}&id=${event_id}`).then(r => r.json())
            ctx.commit("setBought", data.type)
        },
        async getBuyedEvents(ctx) {
            var data = await fetch(`https://freeticket.herokuapp.com/user_events?token=${ctx.state.token}`).then(r => r.json())
            ctx.commit("setBuyedEvents", data.items)
        },
        async getUrlAdmin(ctx) {
            var data = await fetch(`https://freeticket.herokuapp.com/admin/permit_url?token=${ctx.state.token}`).then(r => r.json())
            ctx.commit("setUrl", data.url)
        }
    },
    mutations: {
        authorize(state, { id, type_user, token, first_name, last_name, password, email, money }) {
            state.isAuthorized = true
            state.token = token
            state.first_name = first_name
            state.last_name = last_name
            state.user_type = type_user
            state.user_id = id
            state.balance = money
            window.localStorage.setItem("email", email)
            window.localStorage.setItem("password", password)
        },
        logout(state) {
            state.isAuthorized = false
            state.token = null
            state.first_name = null
            state.last_name = null
            state.user_type = null
            state.user_id = null
            state.balance = null
            window.localStorage.removeItem("email")
            window.localStorage.removeItem("password")
        },
        setMyEvents(state, items) {
            state.my_events = items
        },
        setBought(state, bool) {
            state.bought = bool
        },
        setUrl(state, url) {
            state.url = url
        },
        setBuyedEvents(state, items) {
            state.buyed_events = items
        }
    },
    state: {
        first_name: null,
        last_name: null,
        user_id: null,
        isAuthorized: false,
        token: null,
        user_type: null,
        bought: false,
        my_events: [],
        buyed_events: [],
        balance: null,
        url: ""
    },
    getters: {

    }
}