<template>
  <v-row align="center" justify="center">
    <v-col cols="12" sm="8" md="4">
      <v-card class="elevation-12">
        <v-toolbar color="grey darken-3" dark flat>
          <v-toolbar-title>Создание мероприятия</v-toolbar-title>
          <div class="flex-grow-1"></div>
        </v-toolbar>
        <v-card-text>
          <v-form v-model="valid">
            <v-text-field
              label="Название"
              name="name"
              v-model="event_by_id.name"
              :rules="rules.nameRule"
              color="grey darken-3"
            ></v-text-field>
            <v-text-field
              label="Краткое описание"
              :rules="rules.descriptionRule"
              name="description"
              v-model="event_by_id.description"
              color="grey darken-3"
            ></v-text-field>
            <v-textarea
              label="Описание"
              name="full_desctiption"
              :rules="rules.full_descriptionRule"
              v-model="event_by_id.full_description"
              color="grey darken-3"
            ></v-textarea>
            <v-file-input
              :rules="rules.imageRule"
              color="grey darken-3"
              counter
              @change="onChangeFile"
              label="Изображение"
              placeholder="Выберите изображение для загрузки"
              prepend-icon="mdi-paperclip"
              outlined
              type="file"
              :show-size="1000"
            ></v-file-input>
            <v-text-field
              :rules="rules.intRule"
              label="Стоимость билета"
              name="cost"
              v-model="event_by_id.cost"
              color="grey darken-3"
            ></v-text-field>
            <v-text-field
              label="Место проведения мероприятия"
              :rules="rules.placeRule"
              name="cost"
              v-model="event_by_id.place"
              color="grey darken-3"
            ></v-text-field>Укажите ссылку на организатора или на группу мероприятия
            <v-text-field
              label="Ссылка"
              :rules="rules.urlsRule"
              name="urls"
              v-model="event_by_id.urls"
              color="grey darken-3"
            ></v-text-field>
            <v-col class="shrink">
              <v-img v-if="event_by_id.image" :src="event_by_id.image" width="200" height="200"></v-img>
            </v-col>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="grey darken-3" :dark="valid" @click="edit_event" :disabled="!valid">Изменить</v-btn>
        </v-card-actions>
      </v-card>
      <v-row justify="center">
        <v-dialog v-model="dialog" persistent max-width="290">
          <v-card>
            <v-card-title>Успешно изменено!</v-card-title>
            <v-card-actions>
              <div class="flex-grow-1"></div>
              <v-btn color="green darken-1" text outlined @click="link_to_event">Хорошо</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { isNull } from "util";
export default {
  props: ["id"],
  data() {
    return {
      valid: false,
      dialog: false,
      rules: {
        intRule: [
          v => !!v || "Требуется стоимость",
          v => !isNaN(parseInt(v)) || "Нужна числовая стоимость"
        ],
        imageRule: [v => !!v || "Требуется изображение"],
        nameRule: [v => !!v || "Требуется название"],
        descriptionRule: [v => !!v || "Требуется краткое описание"],
        full_descriptionRule: [v => !!v || "Требуется описание"],
        placeRule: [v => !!v || "Требуется место проведения"],
        urlsRule: [v => !!v || "Требуется ссылка"]
      }
    };
  },
  methods: {
    ...mapActions("UserData", ["editEventAction"]),
    ...mapActions("Events", ["getEventById"]),
    onChangeFile(e) {
      if (isNull(e)) {
        this.event_by_id.image = "";
      } else {
        var reader = new FileReader();
        reader.onload = event => {
          var dataUrl = event.target.result;
          this.event_by_id.image = dataUrl;
          console.log(dataUrl);
        };
        reader.readAsDataURL(e);
      }
    },
    edit_event() {
      this.editEventAction({
        id: this.id,
        place: this.event_by_id.place,
        cost: this.event_by_id.cost,
        description: this.event_by_id.description,
        full_description: this.event_by_id.full_description,
        image: this.event_by_id.image,
        name: this.event_by_id.name,
        urls: this.event_by_id.urls
      }).then(r => (this.dialog = true));
    },
    link_to_event() {
      this.$router.push(`/event/${this.id}`);
    }
  },
  computed: {
    ...mapState("Events", ["event_by_id"])
  },
  mounted() {
    this.getEventById(this.id);
  }
};
</script>
