<template>
  <div>
    <v-item-group>
      <v-row>
        <v-col v-for="user in filteredUsers" :key="user.id" cols="12" md="4">
          <v-item>
            <v-card class="mx-auto" max-width="500">
              <v-card-title class="headline">{{user.first_name}} {{user.last_name}}</v-card-title>
              <v-card-text>
                <div>Идентификатор: {{user.id}}</div>

                <div>Тип аккаунта: {{user.type_user != 'client'? "Организатор" : "Клиент"}}</div>
                <div>EMail: {{user.email}}</div>

                <div v-if="user.buyed_events.length >= 0">
                  <v-divider></v-divider>
                  <div>Куплено билетов: {{user.buyed_events.length}}</div>
                  <v-btn>Подробнее о покупках</v-btn>
                </div>
                <div v-if="user.events.length >= 0">
                  <v-divider></v-divider>
                  <div>Количество мероприятий: {{user.events.length}}</div>
                  <v-btn>Подробнее о мероприятиях</v-btn>
                </div>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="error"
                  v-if="user.type_user == 'org'"
                  @click="replaceTypeUser({type: 'client', user_id: user.id}); user.type_user='client'"
                >
                  <v-icon>mdi-close</v-icon>Клиент
                </v-btn>
                <v-btn
                  color="success"
                  v-if="user.type_user == 'client'"
                  @click="replaceTypeUser({type: 'org', user_id: user.id}); user.type_user='org'"
                >
                  <v-icon>mdi-check-bold</v-icon>Организатор
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-item>
        </v-col>
      </v-row>
    </v-item-group>
  </div>
</template>


<script>
import { mapState, mapActions } from "vuex";
export default {
  props: ["token"],
  data() {
    return {
      search: "",
      users_fetch: []
    };
  },
  methods: {
    ...mapActions("Admin", [
      "getAllUsers",
      "login_admin_action",
      "replaceTypeUser"
    ])
  },
  mounted() {
    if (!this.token) {
      var token = window.localStorage.getItem("adm_item");
    } else {
      var token = this.token;
    }
    this.login_admin_action(token).then(r => {
      this.getAllUsers().then(r => {
        this.users_fetch = this.users;
      });
    });
  },
  computed: {
    ...mapState("Admin", ["users"]),
    filteredUsers() {
      return this.users_fetch;
    }
  }
};
</script>