
  <template>
  <v-row align="center" justify="center">
    <v-col cols="12" sm="8" md="4">
      <v-card
        v-if="event_by_id.moder_status > 0 || user_id == event_by_id.user"
        class="mx-auto"
        color="grey lighten-3"
      >
        <v-img :src="event_by_id.image"></v-img>
        <v-card-text class="pt-6" style="position: relative;">
          <div>Описание: {{event_by_id.full_description}}</div>
          <h3>
            Организатор:
            <a :href="event_by_id.urls" target="_blank">{{event_by_id.urls}}</a>
          </h3>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-text class="pt-6" style="position: relative;">
          <div>
            <v-icon>mdi-map-marker</v-icon>
            {{event_by_id.place}}
            <br />
            <v-icon left>mdi-currency-rub</v-icon>
            {{event_by_id.cost}}
          </div>
          <div v-if="user_id == event_by_id.user">
            <v-icon>mdi-account-alert</v-icon>
            Статус: {{ event_by_id.moder_status | statusCheck}}
            <br />
            Идентификатор: {{id}}
          </div>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <template v-if="user_id != event_by_id.user">
            <v-btn color="grey darken-3" dark v-if="!isAuthorized" to="/login">Купить</v-btn>
            <template v-else>
              <v-btn color="grey darken-3" dark @click="dialog = true">Купить</v-btn>
              <v-row justify="center">
                <v-dialog v-model="dialog" max-width="290">
                  <v-card>
                    <v-card-title class="headline">Покупка билетов</v-card-title>
                    <v-card-text>
                      Количество билетов:
                      <number-input v-model="countTickets" :min="1" controls :inputtable="false"></number-input>
                    </v-card-text>
                    <v-card-actions>
                      Итого: {{ event_by_id.cost * parseInt(countTickets)}}
                      <v-icon>mdi-currency-rub</v-icon>
                      <div class="flex-grow-1"></div>
                      <form method="POST" action="https://money.yandex.ru/quickpay/confirm.xml">
                        <input type="hidden" name="receiver" value="4100110208906384" />
                        <input type="hidden" name="formcomment" value="Надо купить билет" />
                        <input type="hidden" name="label" :value="`event${id} user${user_id}`" />
                        <input type="hidden" name="quickpay-form" value="donate" />
                        <input type="hidden" name="targets" value="Покупка" />
                        <input
                          type="hidden"
                          name="sum"
                          :value="event_by_id.cost*countTickets"
                          data-type="number"
                        />
                        <input
                          type="hidden"
                          name="comment"
                          :value="`Покупка билета на мероприятие ${event_by_id.name} x${countTickets}`"
                        />
                        <input type="hidden" name="paymentType" value="AC" />

                        <v-btn type="submit" color="grey darken-3" dark>Купить</v-btn>
                      </form>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-row>
            </template>
          </template>
          <v-spacer></v-spacer>
          <v-btn
            color="grey darken-3"
            dark
            v-if="user_id == event_by_id.user"
            :to="`/edit_event/${id}`"
          >
            <v-icon>mdi-square-edit-outline</v-icon>Изменить
          </v-btn>
          <v-btn color="error" dark v-if="user_id == event_by_id.user" :to="`/delete_event/${id}`">
            <v-icon>mdi-close</v-icon>Удалить
          </v-btn>
        </v-card-actions>
      </v-card>
      <v-card v-else>
        <v-card-title>Мероприятие не прошло модерацию</v-card-title>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  props: ["id"],
  data() {
    return {
      dialog: false,
      countTickets: 1,
      valid: false,
      rules: {
        countTickets: [
          v =>
            !isNaN(parseInt(v)) || "Количество билетов является только числом"
        ]
      }
    };
  },
  methods: {
    ...mapActions("Events", ["getEventById"]),
    ...mapActions("UserData", ["getIsBought"])
  },
  mounted() {
    this.getEventById(this.id);
    this.getIsBought(this.id);
  },
  filters: {
    statusCheck(v) {
      switch (v) {
        case -1:
          return "Отказано";
        case 0:
          return "В расмотрении";
        case 1:
          return "Разрешено";
      }
    }
  },
  computed: {
    ...mapState("Events", ["event_by_id"]),
    ...mapState("UserData", ["user_id", "isAuthorized", "bought"])
  }
};
</script>

