<template>
  <v-row align="center" justify="center">
    <v-col cols="12" sm="8" md="4">
      <v-card class="elevation-12">
        <v-toolbar color="grey darken-3" dark flat>
          <v-toolbar-title>Авторизация</v-toolbar-title>
          <div class="flex-grow-1"></div>
        </v-toolbar>
        <v-card-text>
          <v-form v-model="valid">
            <v-text-field
              label="Электронная почта"
              name="login"
              prepend-icon="mdi-account"
              type="email"
              :rules="emailRules"
              :error-messages="error"
              @change="error = ''"
              v-model="email"
              color="grey darken-3"
            ></v-text-field>
            <v-text-field
              id="password"
              label="Пароль"
              name="password"
              prepend-icon="mdi-lock"
              :error-messages="error"
              :rules="passwordRule"
              @change="error = ''"
              v-model="password"
              type="password"
              color="grey darken-3"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn color="grey darken-3" outlined dark to="/register">Не зарегистрирован?</v-btn>
          <div class="flex-grow-1"></div>
          <v-btn :disabled="!valid" :dark="valid" color="grey darken-3" @click="login_user">Войти</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      email: "",
      password: "",
      error: "",
      valid: false,
      emailRules: [
        v => !!v || "Требуется электронная почта",
        v => /.+@.+\..+/.test(v) || "Невалидная электронная почта"
      ],
      passwordRule: [v => !!v || "Требуется пароль"]
    };
  },
  methods: {
    ...mapActions("UserData", ["login_user_action"]),
    login_user() {
      this.login_user_action({
        email: this.email,
        password: this.password
      }).then(x => {
        if (x) {
          this.$router.push("/");
        } else {
          this.error = "Неверный логин или пароль";
        }
      });
    }
  }
};
</script>
