<template>
  <v-item-group>
    <v-row>
      <v-col v-for="event in my_events" :key="event.id" cols="12" md="4">
        <v-item>
          <v-card class="mx-auto" max-width="500" @click="link_to_event(event)">
            <v-img height="300px" :src="event.image"></v-img>
            <v-card-text>
              <v-card-title dark>{{event.name}}</v-card-title>
              <div>Краткое описание: {{event.description}}</div>
            </v-card-text>
            <v-divider class="mx-4"></v-divider>
            <v-card-actions>
              <v-icon left>mdi-currency-rub</v-icon>
              <h4>{{event.cost}}</h4>
            </v-card-actions>
          </v-card>
        </v-item>
      </v-col>
    </v-row>
  </v-item-group>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  methods: {
    ...mapActions("UserData", ["getMyEvents"]),
    link_to_event(event) {
      this.$router.push(`/event/${event.id}`);
    }
  },
  mounted() {
    this.getMyEvents();
  },
  computed: {
    ...mapState("UserData", ["my_events"])
  }
};
</script>