<template>
  <div>
    <div v-if="!check_url('/admin')">
      <v-app-bar app dark class="grey darken-3">
        <v-app-bar-nav-icon @click="onClickMenu" class="hidden-md-and-up"></v-app-bar-nav-icon>
        <v-toolbar-items>
          <v-btn text v-text="'FreeTicket'" to="/"></v-btn>
        </v-toolbar-items>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn
            text
            v-for="(item, index) in filteredMenuItems"
            :key="index"
            :to="item.route"
            class="hidden-sm-and-down"
          >
            <v-icon left>mdi-{{item.icon}}</v-icon>
            {{item.title}}
          </v-btn>
          <v-btn text v-if="isAuthorized == false" to="/login">
            <v-icon left>mdi-login</v-icon>Войти
          </v-btn>
          <v-btn text fab v-if="isAuthorized == true" to="/profile">
            <v-icon>mdi-account</v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-app-bar>
      <v-navigation-drawer
        color="grey darken-3"
        temporary
        absolute
        v-model="drawer"
        class="hidden-md-and-up"
      >
        <v-list dense nav>
          <div v-for="(item, index) in filteredMenuItems" :key="index">
            <v-list-item link :to="item.route" dark color="success">
              <v-list-item-icon>
                <v-icon>mdi-{{ item.icon }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </div>
        </v-list>
      </v-navigation-drawer>
    </div>
    <div v-else>
      <v-app-bar app dark class="grey darken-3">
        <v-app-bar-nav-icon @click="drawer = true" class="hidden-md-and-up"></v-app-bar-nav-icon>
        <v-toolbar-items>
          <v-btn text v-text="'FreeTicket'" to="/"></v-btn>
        </v-toolbar-items>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn text to="/admin/event_moder" class="hidden-sm-and-down">
            <v-icon left>mdi-calendar-check-outline</v-icon>Модерация мероприятий
          </v-btn>
          <v-btn text to="/admin/user_moder" class="hidden-sm-and-down">
            <v-icon left>mdi-account-check-outline</v-icon>Модерация пользователей
          </v-btn>
        </v-toolbar-items>
      </v-app-bar>
      <v-navigation-drawer
        color="grey darken-3"
        temporary
        absolute
        v-model="drawer"
        class="hidden-md-and-up"
      >
        <v-list dense nav dark>
          <v-list-item link to="/admin/event_moder" color="success">
            <v-list-item-icon>
              <v-icon>mdi-calendar-check-outline</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Модерация мероприятий</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item link to="/admin/user_moder" color="success">
            <v-list-item-icon>
              <v-icon>mdi-calendar-check-outline</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>Модерация пользователей</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { isNull } from "util";
export default {
  computed: {
    ...mapState("UserData", ["isAuthorized", "user_type"]),
    filteredMenuItems() {
      return this.menuItems.filter(v => {
        var result = v.need_auth == this.isAuthorized || isNull(v.need_auth);
        if (!!v.require_org) {
          result = result && this.user_type == "org";
        }
        return result;
      });
    }
  },
  data() {
    return {
      drawer: false,
      menuItems: [
        {
          icon: "account-group",
          title: "Мои мероприятия",
          route: "/my_events",
          need_auth: true,
          require_org: true
        },
        {
          icon: "plus",
          title: "Добавить мероприятие",
          route: "/add_event",
          need_auth: true,
          require_org: true
        },
        {
          icon: "account-details",
          title: "Организаторам",
          route: "for_organization",
          need_auth: true
        },
        {
          icon: "currency-rub",
          title: "Наши услуги",
          route: "donate",
          need_auth: true
        },
        {
          icon: "information-outline",
          title: "О нас",
          route: "/about",
          need_auth: true
        }
      ]
    };
  },
  methods: {
    onClickMenu() {
      this.drawer = true;
    },
    check_url(v) {
      if (this.$route.fullPath.startsWith(v)) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>