import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Register from './views/Register.vue'
import Logout from './views/Logout.vue'
import Add_Event from './views/Add_Event.vue'
import Event from "./views/Event.vue"
import Profile from "./views/Profile.vue"
import Edit_Event from './views/Edit_Event.vue'
import Delete_Event from './views/Delete_Event.vue'
import My_Events from './views/My_Events.vue'
import Admin from './views/Admin.vue'
import store from './store'
import ForOrganizations from './views/ForOrganizations.vue'
import UserModer from './views/AdminPanel/Users_Moder.vue'
import EventsModer from './views/AdminPanel/Events_Moder.vue'

Vue.use(Router)
var router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: "/logout",
      name: "logout",
      component: Logout
    },
    {
      path: "/add_event",
      name: "add_event",
      component: Add_Event
    },
    {
      path: "/edit_event/:id",
      name: "edit_event",
      component: Edit_Event,
      props: true
    },
    {
      path: "/delete_event/:id",
      name: "delete_event",
      component: Delete_Event,
      props: true
    },
    {
      path: "/event/:id",
      name: "event_by_id",
      component: Event,
      props: true
    },
    {
      path: "/my_events",
      name: "my_events",
      component: My_Events
    },
    {
      path: "/profile",
      name: "profile",
      component: Profile
    },
    {
      path: "/admin",
      name: "admin",
      component: Admin,
      props: (route) => ({ token: route.query.token })
    },
    {
      path: "/for_organization",
      name: "for_organization",
      component: ForOrganizations
    },
    {
      path: "/admin/user_moder",
      name: "admin_user_moder",
      component: UserModer
    },
    {
      path: "/admin/event_moder",
      name: "admin_event_moder",
      component: EventsModer
    }
  ]
})

router.beforeEach((to, from, next) => {
  var email = window.localStorage.getItem("email")
  var password = window.localStorage.getItem("password")
  var token = store.state.UserData.token
  if (!!email && !!password && !token) {
    store.dispatch("UserData/login_user_action", { email, password }).then(r => next())
  } else {
    next()
  }
})

export default router