export default {
    namespaced: true,
    actions: {
        async login_admin_action(ctx, token) {
            return await fetch(`https://freeticket.herokuapp.com/admin/login?token=${token}`).then(r => r.json()).then(r => {
                if (r.result == true)
                    ctx.commit("setTokenAdmin", token)
            })
        },
        async setStatusEvent(ctx, { id, status }) {
            return await fetch(`https://freeticket.herokuapp.com/admin/event_set_status?id=${id}&token=${ctx.state.token}&status=${status}`).then(r => r.json())
        },
        async getNotModeratedEvents(ctx) {
            var data = await fetch(`https://freeticket.herokuapp.com/admin/get_events_not_moder?token=${ctx.state.token}`).then(r => r.json())
            ctx.commit("setNotModeratedEvents", data.items)
        },
        async getAllUsers(ctx) {
            var data = await fetch(`https://freeticket.herokuapp.com/admin/get_all_users?token=${ctx.state.token}`).then(r => r.json())
            ctx.commit("setAllUsers", data.items)
        },
        async replaceTypeUser(ctx, { type, user_id }) {
            var data = await fetch(`https://freeticket.herokuapp.com/admin/change_type_user?token=${ctx.state.token}&type=${type}&user_id=${user_id}`).then(r => r.json())
        }
    },
    mutations: {
        setTokenAdmin(state, token) {
            state.token = token
            window.localStorage.setItem("adm_item", token)
        },
        setNotModeratedEvents(state, items) {
            state.not_moderated_events = items
        },
        setAllUsers(state, items) {
            state.users = items
        }
    },
    state: {
        token: "",
        isAdmin: false,
        not_moderated_events: [],
        users: []
    }
}