
    <template>
  <v-item-group>
    <v-row>
      <v-col v-for="event in filteredEvents" :key="event.id" cols="12" md="4">
        <v-item>
          <v-card class="mx-auto" max-width="500">
            <v-img height="300px" :src="event.image"></v-img>
            <v-card-text>
              <div>Название: {{event.name}}</div>
              <div>Краткое описание: {{event.description}}</div>
              <div>Описание: {{event.full_description}}</div>
              <div>Местоположение: {{event.place}}</div>
              <div>
                Ссылка:
                <a :href="event.urls">{{event.urls}}</a>
              </div>
            </v-card-text>
            <v-divider class="mx-4"></v-divider>
            <v-card-actions>
              <v-icon left>mdi-currency-rub</v-icon>
              <h4>{{event.cost}}</h4>
              <v-spacer></v-spacer>
              <v-btn color="error" @click="onChangeStatus(event.id, -1)">
                <v-icon>mdi-close</v-icon>Запретить
              </v-btn>
              <v-btn color="success" @click="onChangeStatus(event.id, 1)">
                <v-icon>mdi-check-bold</v-icon>Разрешить
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-item>
      </v-col>
    </v-row>
  </v-item-group>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  props: ["token"],
  data() {
    return {
      filteredEvents: []
    };
  },
  methods: {
    onChangeStatus(id, status) {
      this.setStatusEvent({
        id,
        status
      }).then(r => {
        this.filteredEvents = this.filteredEvents.filter(r => r.id != id);
      });
    },
    ...mapActions("Admin", [
      "login_admin_action",
      "getNotModeratedEvents",
      "setStatusEvent"
    ])
  },
  mounted() {
    if (!this.token) {
      var token = window.localStorage.getItem("adm_item");
    } else {
      var token = this.token;
    }
    this.login_admin_action(token).then(r => {
      this.getNotModeratedEvents().then(r => {
        this.filteredEvents = this.not_moderated_events;
      });
    });
  },
  computed: {
    ...mapState("Admin", ["not_moderated_events"])
  }
};
</script>