<template>
  <v-row align="center" justify="center">
    <v-col cols="12" sm="8" md="4">
      <v-card class="elevation-12">
        <v-toolbar color="grey darken-3" dark flat>
          <v-toolbar-title>Создание мероприятия</v-toolbar-title>
          <div class="flex-grow-1"></div>
        </v-toolbar>
        <v-card-text>
          <v-form v-model="valid">
            <v-text-field
              label="Название"
              name="name"
              v-model="name"
              :rules="rules.nameRule"
              color="grey darken-3"
            ></v-text-field>
            <v-text-field
              label="Краткое описание"
              :rules="rules.descriptionRule"
              name="description"
              v-model="description"
              color="grey darken-3"
            ></v-text-field>
            <v-textarea
              label="Описание"
              name="full_desctiption"
              :rules="rules.full_descriptionRule"
              v-model="full_description"
              color="grey darken-3"
            ></v-textarea>
            <v-file-input
              :rules="rules.imageRule"
              color="grey darken-3"
              counter
              @change="onChangeFile"
              label="Изображение"
              placeholder="Выберите изображение для загрузки"
              prepend-icon="mdi-paperclip"
              outlined
              type="file"
              :show-size="1000"
            ></v-file-input>
            <v-text-field
              :rules="rules.intRule"
              label="Стоимость билета"
              name="cost"
              v-model="cost"
              color="grey darken-3"
            ></v-text-field>
            <v-text-field
              label="Место проведения мероприятия"
              :rules="rules.placeRule"
              name="cost"
              v-model="place"
              color="grey darken-3"
            ></v-text-field>Укажите ссылку на организатора или на группу мероприятия
            <v-text-field
              label="Ссылка"
              :rules="rules.urlsRule"
              name="urls"
              v-model="urls"
              color="grey darken-3"
            ></v-text-field>
            <v-col class="shrink">
              <v-img v-if="url" :src="url" width="200" height="200"></v-img>
            </v-col>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn
            color="grey darken-3"
            :dark="valid"
            @click="create_event"
            :disabled="!valid"
          >Создать</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import { mapActions } from "vuex";
import { isNull } from "util";
export default {
  data() {
    return {
      name: "",
      full_description: "",
      description: "",
      cost: "",
      place: "",
      url: "",
      urls: "",
      valid: false,
      rules: {
        intRule: [
          v => !!v || "Требуется стоимость",
          v => !isNaN(parseInt(v)) || "Нужна числовая стоимость"
        ],
        imageRule: [v => !!v || "Требуется изображение"],
        nameRule: [v => !!v || "Требуется название"],
        descriptionRule: [v => !!v || "Требуется краткое описание"],
        full_descriptionRule: [v => !!v || "Требуется описание"],
        placeRule: [v => !!v || "Требуется место проведения"],
        urlsRule: [v => !!v || "Требуется ссылка"]
      }
    };
  },
  methods: {
    ...mapActions("UserData", ["addEventAction"]),
    onChangeFile(e) {
      if (isNull(e)) {
        this.url = "";
      } else {
        var reader = new FileReader();
        reader.onload = event => {
          var dataUrl = event.target.result;
          this.url = dataUrl;
          console.log(dataUrl);
        };
        reader.readAsDataURL(e);
      }
    },
    create_event() {
      this.addEventAction({
        place: this.place,
        cost: this.cost,
        description: this.description,
        full_description: this.full_description,
        image: this.url,
        name: this.name,
        urls: this.urls
      }).then(r => this.$router.push(`/event/${r}`));
    }
  }
};
</script>
