

export default {
    namespaced: true,
    actions: {
        async getRecentlyEvents(ctx) {
            var data = await fetch("https://freeticket.herokuapp.com/recent_events")
            data = await data.json()
            ctx.commit("setRecentlyEvents", data.items)
        },
        async getEventById(ctx, id) {
            var data = await fetch(`https://freeticket.herokuapp.com/get_event?id=${id}`)
            data = await data.json()
            ctx.commit("setEventById", data.items)
        }
    },
    mutations: {
        setRecentlyEvents(state, items) {
            state.recently_events = items
        },
        setEventById(state, item) {
            state.event_by_id = item
        }

    },
    state: {
        recently_events: [],
        event_by_id: {}
    },
    getters: {

    }
}