<template>
  <v-row>
    <v-col>
      <v-card class="elevation-12">
        <v-toolbar color="grey darken-3" dark flat>
          <v-toolbar-title>Профиль</v-toolbar-title>
          <div class="flex-grow-1"></div>
        </v-toolbar>
        <v-card-text class="body-1">
          <v-icon>mdi-account</v-icon>
          {{first_name}} {{last_name}}
          <br />
          Тип аккаунта: {{user_type == "client"? "Клиент": "Организатор"}}
          <br />
          Индентификатор: {{user_id}}
          <template v-if="user_type == 'org'">
            <br />
            Баланс: {{balance}}
            <v-icon>mdi-currency-rub</v-icon>
          </template>
        </v-card-text>

        <v-card-actions>
          <v-btn dark color="grey darken-3" to="/logout">
            <v-icon>mdi-logout</v-icon>Выйти
          </v-btn>
          <v-btn dark color="grey darken-3" :to="url" v-if="!!url">
            <v-icon>mdi-login</v-icon>Перейти в админ панель
          </v-btn>
          <div class="flex-grow-1"></div>
        </v-card-actions>
      </v-card>
      <v-card class="elevation-12 mt-3" v-if="buyed_events.length > 0">
        <v-toolbar color="grey darken-3" dark flat>
          <v-toolbar-title>Купленные билеты</v-toolbar-title>
          <div class="flex-grow-1"></div>
        </v-toolbar>
        <v-item-group>
          <v-row>
            <v-col v-for="event in buyed_events" :key="event.id" cols="12" md="4">
              <v-item>
                <v-card class="mx-auto" max-width="500" @click="link_to_event(event)">
                  <v-img height="300px" :src="event.image"></v-img>
                  <v-card-text>
                    <v-card-title dark>{{event.name}}</v-card-title>
                    <div>Краткое описание: {{event.description}}</div>
                  </v-card-text>
                  <v-divider class="mx-4"></v-divider>
                  <v-card-actions>
                    <v-icon left>mdi-currency-rub</v-icon>
                    <h4>{{event.cost}}</h4>
                  </v-card-actions>
                </v-card>
              </v-item>
            </v-col>
          </v-row>
        </v-item-group>
        <v-card-actions>
          <div class="flex-grow-1"></div>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  methods: {
    ...mapActions("UserData", ["getBuyedEvents", "getUrlAdmin"]),
    link_to_event(event) {
      this.$router.push(`/event/${event.id}`);
    }
  },
  mounted() {
    this.getBuyedEvents();
    this.getUrlAdmin();
  },
  computed: {
    ...mapState("UserData", [
      "first_name",
      "last_name",
      "user_type",
      "user_id",
      "url",
      "balance",
      "buyed_events"
    ])
  }
};
</script>